<template>
  <div id="nc"></div>
</template>


<script>
export default {
  props: {
    redObj: Object
  },
  data() {
    return {};
  },
  mounted() {
    let _this = this;
    var nc_token = ["CF_APP_1", new Date().getTime(), Math.random()].join(":");
    var nc = NoCaptcha.init({
      renderTo: "#nc",
      appkey: "CF_APP_1",
      scene: "register",
      token: nc_token,
      trans: {
        key1: "code0"
      },
      bannerHidden: false,
      callback: function(data) {
        _this.$emit("validUser");
        setTimeout(() => {
          nc.reset(); //请务必确保这里调用一次reset()方法
        }, 1000);
      },
      error: function(s) {}
    });
    NoCaptcha.setEnabled(true);
    NoCaptcha.upLang("cn", {
      LOADING: _this.$t('Loading'), //加载
      SLIDER_LABEL: _this.$t('NZInfo1'), //等待滑动
      CHECK_Y: _this.$t('NZInfo2') //通过
    });
  },

  methods: {},
  computed: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



